.popup-feedback__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
}

.popup-feedback__description {
  line-height: 24px;
  color: #333333;
}

.popup-feedback__down-part {
  background-color: @back-gray;
}

.popup-feedback__close {
  margin: 20px;
  padding: 18px 62px 14px 60px;
  background-color: @back-blue;
  color: @white;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
}
